import React from "react"
import logo from "../static/logo/logo.png"
import home from "../static/icons/home.svg"
import about from "../static/icons/about.svg"
import portfolio from "../static/icons/portfolio.svg"
import linkedin from "../static/icons/linkedin.svg"
import instagram from "../static/icons/instagram.svg"
import github from "../static/icons/github.svg"

export const NavBar = () => {
    return (<nav className="navigation">
        
        <ul>
            <li>
                <button>
                    <img className="icon" src={logo} alt="eliasercs" width={40} height={40} />
                    <span className="title">eliasercs</span>
                </button>
            </li>
        </ul>

        <ul>
            <li className="list">
                <button>
                    <img className="icon" src={home} alt="Inicio" />
                    <span className="title">Home</span>
                </button>
            </li>
            <li className="list">
                <button>
                    <img className="icon" src={about} alt="Acerca de mí" />
                    <span className="title">About</span>
                </button>
            </li>
            <li className="list">
                <button>
                    <img className="icon" src={portfolio} alt="Proyectos" />
                    <span className="title">Projects</span>
                </button>
            </li>
        </ul>

        <ul>
            <li className="list">
                <button>
                    <img className="icon" src={linkedin} alt="Perfil en Linkedin" />
                    <span className="title">Linkedin</span>
                </button>
            </li>
            <li className="list">
                <button>
                    <img className="icon" src={instagram} alt="Perfil en Instagram" />
                    <span className="title">Instagram</span>
                </button>
            </li>
            <li className="list">
                <button>
                    <img className="icon" src={github} alt="Repositorio de código" />
                    <span className="title">GitHub</span>
                </button>
            </li>
        </ul>
        
    </nav>)
}